import React from 'react';
import { Flex, Box, Button, Text } from 'rebass';
import { useStaticQuery, graphql } from "gatsby";
import styled from '@emotion/styled/macro';

import IndividualProduct from './IndividualProduct';
import Pagination from '../../components/Pagination';
import CollectionStats from '../../components/CollectionStats';
import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';

const StyledBox = styled(Box)`
  text-align: center;
`

const CollectionDesc = styled(Box)`
  line-height: 1.67;
`

const CollectionTabs = styled(Tabs)`
  border-top: 1px solid #e6e6e6;
  padding: 0 6vw;
`

function CatalogPage(props) {
  const { gatsbyImageProps } = props.data.store.siteMetadata.gatsbyStorefrontConfig;
  const { limit, skip, cartUrl } = props.pageContext;
  const { title, handle, description } = props.data.collection.nodes[0];
  let products = [];

  props.data.collection.nodes.forEach((node) => {
    products = [...products, ...node.products];
  });

  const prodType = handle;

  const data = useStaticQuery(graphql`
    {
      tabTitle: allShopifyCollection(filter: {products: {elemMatch: {productType: {in: "face"}}}}) {
        nodes {
          title
          handle
          products {
            id
            shopifyId
            title
            tags
            productType
            fields {
              shopifyThemePath
              firstImage {
                altText
                originalSrc
              }
            }
            availableForSale
            priceRange {
              minVariantPrice {
                amount
                currencyCode
              }
              maxVariantPrice {
                amount
                currencyCode
              }
            }
            variants {
              shopifyId
              availableForSale
              compareAtPrice
              price
            }
          }
        }
      }
    }
  `)

  return (
    <StyledBox pt={['26vw', '19vw', '16vw']} fontSize={['4vw', '2vw', '1.2vw']} sx={{ background: 'url(//cdn.shopify.com/s/files/1/0117/9005/7534/files/bg-face.jpg)' }}>
      {/* Header */}
      <Box px={['10vw', '8vw', '20vw']} pb={['5vw', null, '3vw']}>
        <Box fontSize={['9vw', '5vw', '4vw']}>
          {title}
        </Box>
        <CollectionDesc>  
          {description}
        </CollectionDesc>
      </Box>

      <Box sx={{ fontSize: ['3vw', '1.5vw', '1vw'] }}>
        <Button>test</Button>
        {/* Tabs */}
        <CollectionTabs defaultActiveKey="all">
          <Tab eventKey="all" title={title} sx={{ padding: ['3vw', '1.5vw', '1vw'] }}>
            {/* Products */}
            <Box backgroundColor="background" padding={'3vw'}>
              <Box>
                {products.map((product, index) => {
                  product['cartUrl'] = cartUrl;
                  if (index + 1 > skip && index + 1 <= skip + limit) {
                    return (
                      <IndividualProduct key={product.shopifyId} product={product} />
                    );
                  } else {
                    return '';
                  }
                })}
              </Box>

              {/* Pagination */}
              <Box width={1} key="pagination">
                <Text sx={{ position: 'relative'}}>
                  <Pagination {...props.pageContext} />
                </Text>
              </Box>
            </Box>
          </Tab>
          
          {data.tabTitle.nodes.map((tab, index) => {
            if (tab.handle != prodType) {
              return (
                <Tab id={tab.handle} key={index} eventKey={tab.handle} title={tab.title}>
                  {/* Products */}
                  <Box backgroundColor="background" padding={'3vw'}>
                    <Box>
                      {tab.products.map((product, index) => {
                        product['cartUrl'] = cartUrl;
                        if (index + 1 > skip && index + 1 <= skip + limit) {
                          return (
                            <IndividualProduct key={product.shopifyId} product={product} />
                          );
                        } else {
                          return '';
                        }
                      })}
                    </Box>

                    {/* Pagination */}
                    <Box width={1} key="pagination">
                      <Text sx={{ position: 'relative'}}>
                        <Pagination {...props.pageContext} />
                      </Text>
                    </Box>
                  </Box>
                </Tab>
              )
            } else {''}
          })}
        </CollectionTabs>
      </Box>
    </StyledBox>
  );
}

export default CatalogPage;
