import React from 'react';
import { Flex, Box, Heading, Text } from 'rebass';
import styled from '@emotion/styled/macro';

import IndividualProduct from './IndividualProduct';
import Pagination from '../../components/Pagination';
import CollectionStats from '../../components/CollectionStats';
import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';

const StyledBox = styled(Box)`
  text-align: center;
`

const CollectionDesc = styled(Box)`
  line-height: 1.67;
`

function CatalogPageTeal(props) {
  const { gatsbyImageProps } = props.data.store.siteMetadata.gatsbyStorefrontConfig;
  const { limit, skip, cartUrl } = props.pageContext;
  const { title, description } = props.data.collection.nodes[0];
  let products = [];

  props.data.collection.nodes.forEach((node) => {
    products = [...products, ...node.products];
  });

  return (
    <StyledBox pt={['26vw', '19vw', '16vw']} fontSize={['4vw', '2vw', '1.2vw']} sx={{ background: 'url(//cdn.shopify.com/s/files/1/0117/9005/7534/files/bg-teal.jpg)' }}>
      {/* Header */}
      <Box px={['10vw', '8vw', '20vw']} pb={['5vw', null, '3vw']}>
        <Box fontSize={['9vw', '5vw', '4vw']}>
          {title}
        </Box>
        <CollectionDesc>
          {description}
        </CollectionDesc>
      </Box>
        
      {/* Products */}
      <Box backgroundColor="background" padding={'3vw'}>
        <Box>
          {products.map((product, index) => {
            product['cartUrl'] = cartUrl;
            if (index + 1 > skip && index + 1 <= skip + limit) {
              return (
                <IndividualProduct key={product.shopifyId} product={product} />
              );
            } else {
              return '';
            }
          })}
        </Box>

        {/* Pagination */}
        <Box width={1} key="pagination">
          <Text sx={{ position: 'relative'}}>
            <Pagination {...props.pageContext} />
          </Text>
        </Box>
      </Box>
    </StyledBox>
  );
}

export default CatalogPageTeal;
